* {
  font-family: montserrat;
}
:root {
    --darkPink: #d41876;
    --lightPink: #ff42a1;
    --darkPrimary: #0433ff;
    --lightPrimary: #00a1ff;
    --dark: #000000;
    --light: #fff;
    --red: #ed220d;
    --green: #60d937;
    --regular: regular-josefina;
    --medium: medium-josefina;
    --bold: bold-josefina;
    --borderColor: rgba(0, 0, 0, 0.1);
  }
.questions_content {
    width: 100%;
    height: 80px;
    background: var(--lightPrimary);
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 3px;
    margin: 0 auto;
    margin-bottom: 1rem;
  }

  .questions_content h2 {
    color: #ffff;
    font-weight: 900;
    font-size: 25px;
    font-family: ui-rounded;
  }
  
  .questions_content span {
    padding: 0.5rem;
    background: red;
  }
  
  .questions_content #number_1,
  .questions_content #number_2 {
    padding: 1rem;
    min-width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    border-radius: 8px;
    background: var(--lightPink);
    color: var(--light);
    font-size: 35px;
    font-family: var(--medium);
  }
  
  .questions_content #operation {
    padding: 1rem;
    width: 20px;
    height: 20px;
    text-align: center;
    border-radius: 8px;
    background: var(--green);
    color: var(--dark);
    font-size: 40px;
    font-family: var(--bold);
  }
  
  /* End Questions container */
  
  /* Start Answers container */
  .answers_content {
    width: 100%;
    height: 300px;
    margin: 0 auto;
  }
  
  .answers_content .row {
    display: flex;
    flex-direction: column;
    /* justify-content: space-around; */
    /* align-items: flex-start; */
    margin: 2rem 0;
  }
  
  .answers_content .row .answer_box {
    padding: 0.5rem 1rem;
    display: flex;
    /* flex-direction:column; */
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    /* border-radius: 35px 20px; */
    margin-right: 15px;
    /* width: 258px; */
    width: 100%;
    margin-bottom: 20px;
    align-items: center;
    transition: 0.1s transform linear;
    cursor: pointer;
    flex: 1;
  }
  
  .answers_content .row .answer_box:hover {
    border: 0.5px solid var(--borderColor);
  }
  
  .answers_content .row .answer_box:active {
    transform: scale(0.93);
  }
  
  .answers_content .row .answer_box .answer_btn {
    width: 60px;
    height: auto;
    /* height: 60px; */
    background: var(--darkPrimary);
    font-size: 25px;
    color: var(--light);
    font-family: var(--medium);
    border-radius: 30px;
    text-align: center;
    line-height: 60px;
    margin-right: 1rem;
  }

  /* .answer-box {
} */

.answers_content .row .answer_box .answer_text {
    font-size: 24px;
    font-family: var(--bold);
  }

  .wrap {
    margin: auto;
    width: 750px;
    height: auto;
    box-shadow: 1px 0px 15px #000000;
  }

  .gif {
    width: 50%;
    margin: auto;
    border-radius: 50%;
  }

  .res-title {
    font-family: ui-rounded;
    border-radius: 38px 19px;
    color: #ffffff;
    /* background-color: #00a1ff; */
    background: #2193b0;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #6dd5ed, #2193b0);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #6dd5ed, #2193b0); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  }